      custAmountBal(custNum) {
        console.log(custNum);
        var account = this.idList.filter(function (record) {
          if (record.accountId == custNum) {
            return record;
          }
        });
        console.log(account);
        this.paymentForm.controls.accountCurrency.setValue(account[0]?.accountCurrency?account[0]?.accountCurrency:this.accountCurrency1);
        if (account.length > 0) {
          this.accNum = account[0].accountId;
          if(account[0].amount == 0){
            this.accountBal = "00000";
          }else{
            this.accountBal = account[0].amount;
          }
          this.selectedAccountInfo = account[0];
        }
        console.log(this.accountBal);
        this.paymentForm.controls.accountBalance.setValue(this.accountBal);
        console.log(this.paymentForm);
        // localStorage.setItem('AccountBal', this.accountBal);
      }
      ngOnDestroy() {
        this.subscriptions.forEach((s) => s.unsubscribe());
      }
    
      onChange: any = () => {};
      onTouched: any = () => {};
    
      registerOnChange(fn) {
        this.onChange = fn;
      }
    
      writeValue(value) {
        if (value) {
          this.value = value;
        }
    
        if (value === null) {
          this.paymentForm.reset();
        }
      }
    
      registerOnTouched(fn) {
        this.onTouched = fn;
      }
    
      validate(_: FormControl) {
        return this.paymentForm.valid ? null : { profile: { valid: false } };
      }
      addCurrencyImage(currency) {
        return currency.toLowerCase();
      }
      gettingallTansactionDetails(loanData) {
       console.log(loanData)
       this.currencyImage = loanData[0].accountCurrency? "currency-flag-"+loanData[0].accountCurrency.toLowerCase() : "currency-flag-"+loanData[0].accountCurrency.toLowerCase();
          this.paymentForm.controls.transactionCurrency.setValue(loanData[0].accountCurrency);
          this.paymentForm.controls.rePaymentMode.setValue('Cash');
          this.accountCurrency=loanData[0].accountCurrency;
          if(this.titleVal=='Repayment'){
          this.paymentForm.controls.loanRepaymentAmount.setValue(loanData[0]?.currentArrears?loanData[0]?.currentArrears:0);
          }
          this.dataService.sendPaymentInfoFormData(this.paymentForm.value);
      }
